.container {
    display: flex;
    flex-direction: column;
    width: 20%;
    height: auto;
    background-color: #232321;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.project_banner {
    border-radius: 8px 8px 0px 0px;
}

.project_info {
    padding: 8px 8px;
}

.project_name {
    font-family: 'SFPro-semibold';
    font-size: 1.2rem;
    margin-bottom: 8px;
}

.project_description {
    font-size: 0.8rem;
}
